class PersistentDrawer extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      open: false,
      anchor: 'left',
    };
}
  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  handleChangeAnchor = event => {
    this.setState({
      anchor: event.target.value,
    });
  };

  handlePageChange = (evt) => {
    const that = this;
    return (page)=>{
      that.props.handlePageState(page.target.innerText);
    }
  }

  render() {
    const { classes, theme } = this.props;
    const { anchor, open } = this.state;

    const drawer = (
      <Drawer
        variant="persistent"
        anchor={anchor}
        open={open}

      >
        <div>

          <IconButton onClick={this.handleDrawerClose}>
           <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider />
        <List component="nav">
        <ListItem button component={Link} to="/" onClick={this.handlePageChange("Home")}><ListItemText primary="Home" /></ListItem>
        <Divider />
        <ListItem button component={Link} to="/AboutUs" onClick={this.handlePageChange("About Us")}><ListItemText primary="About Us"/></ListItem>
        <Divider />
        <ListItem button component={Link} to="/Projects" onClick={this.handlePageChange("Projects")}><ListItemText primary="Projects" /></ListItem>
        <Divider />
        <ListItem button component={Link} to="/links" onClick={this.handlePageChange("Links")}><ListItemText primary="Links" /></ListItem>
        <Divider />
        <ListItem button component={Link} to="/gallery" onClick={this.handlePageChange("gallery")}><ListItemText primary="Gallery" /></ListItem>
        <Divider />
        <ListItem button component={Link} to="/contact" onClick={this.handlePageChange("Contact Us")}><ListItemText primary="Contact Us" /></ListItem>
        </List>
      </Drawer>
    );

    let before = null;
    let after = null;

    if (anchor === 'left') {
      before = drawer;
    } else {
      after = drawer;
    }

    console.log(this.props.currentState);

    return (
      <div>

          <AppBar>
            <Toolbar disableGutters={!open}>
              <IconButton
                color="inherit"
                aria-label="Open drawer"
                onClick={this.handleDrawerOpen}
              >
                <MenuIcon />
              </IconButton>
              <Typography variant="title" color="inherit" noWrap>
                {this.props.currentState}
              </Typography>
            </Toolbar>
          </AppBar>
          {before}
          <main>
            <div />
          </main>
          {after}
      </div>
    );
  }
}


