class TopBar extends React.Component{
  constructor(props){
    super(props);

  }
  state = {
   value: 0
 };

  handleValueChange = (evt,value)=>{
    this.setState({value});
    const array = ["Home","About Us","Projects","Links","Gallery","Contact Us"]
    console.log(array[value]);
    this.props.handlePageState(array[value]);
  }

  render(){
    return (<div id="TopBar">
           <Grid container direction="row" spacing={12}>
            <AppBar position="static" color="primary" display="none">
            {(window.devicePixelRatio==1)?(<center><a href="/"><img src="http://rrscw.nrsc.gov.in/rscw/images/logo.png" width="900" height="150" alt="" /></a></center>)
            :(<a href="/"><img src={require("./isro_mlogo.png")} alt="" style={{width:170, height: 170}} /></a>)}

          <Grid container spacing={12} >
          <Grid item lg={1} sm xs />
         <Grid item lg={10} sm xs>
          <Paper>

            <Tabs
               value={this.state.value}
               onChange={this.handleValueChange}
               indicatorColor="primary"
               textColor="primary"
               centered
               scrollable
               scrollButtons="auto"
             >


               <Tab label="Home" component={Link} to="/" />
               <Tab label="About Us" component={Link} to="/AboutUs"/>
               <Tab label="Project" component={Link} to="/Projects" />
               <Tab label="Links" component={Link} to="/links" />
               <Tab label="Gallery" component={Link} to="/gallery" />
               <Tab label="Contact Us" component={Link} to="/contact" />
             </Tabs>

            </Paper>
            </Grid>
            <Grid item lg={1} sm xs></Grid>
            </Grid>

            </AppBar>
              </Grid>

    </div>);
  }
}



