class App extends React.Component{

    componentDidMount (){
      this.setState({height:document.getElementById('TopBar').clientHeight})
      window.addEventListener('scroll', this.handleScroll.bind(this), { passive: true });
    }

    componentWillUnmount() {
     window.removeEventListener('scroll', this.handleScroll);
    }

    constructor(props) {
    super(props);
     this.state ={height:0,bl:false,page:"Home"}
     this.handleScroll = this.handleScroll.bind(this);
    }

    handleScroll(evt) {
    (window.scrollY>=this.state.height)?(this.setState({bl:true})):(this.setState({bl:false}))

  }

  handlePageState = (p)=>{
    console.log(p);
    this.setState({page: p});
  }

    render(){
      console.log(this.state.page)
      return (<div>
       <TopBar handlePageState={this.handlePageState}/>
       {
         (this.state.bl==true)?(<PersistentDrawer handlePageState={this.handlePageState} currentState={this.state.page}/>):(<span></span>)
       }



        <Switch>
          <Route exact path={'/'} render={(prop)=>{
            return <Home {...prop} />
          }} />

         <Route exact path={'/AboutUs'} render={(prop)=>{
            return <AboutUs {...prop} />
          }} />

          <Route exact path={'/Projects'} render={(prop)=>{
            return <Projects {...prop} />
          }} />

        <Route exact path={'/links'} render={(prop) => {
              return <Links {...prop}/>
            }}/>

        <Route exact path={'/gallery'} render={(prop) => {
             return <Gallery {...prop}/>
           }}/>

           <Route exact path={'/contact'} render={(prop) => {
               return <ContactUs {...prop}/>
             }}/>


         </Switch>


      </div>
    );
  }
}


export default App;



